.crumbs {
    margin: 7px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    list-style-type: none;
    gap: 0.1rem;
    transition: all 400ms;
  }
  
  .crumb {
    text-align: center;
    border: 1px solid rgb(80, 41, 89);
    user-select: none;
    margin: 20px 5px;
    font-size: 14px;
    width: 32px;
    display: block;
    padding: 0.5rem .3rem;
    text-decoration: none;
    border-radius: 0.2rem;
    border: 0.0625rem solid #d7d7d7; /* 1px */
    box-shadow: 6px 7px 8px -6px rgba(69,69,69,0.7);
    color: white;
    background-color: rgb(80, 41, 89);
    transition: all 300ms ease-in-out;
  }
  
  .crumb:hover, .crumb__active {
    text-align: center;
    user-select: none;
    margin: 20px 5px;
    font-size: 14px;
    width: 32px;
    display: block;
    padding: 0.5rem .3rem;
    text-decoration: none;
    border-radius: 0.2rem;
    border: 2px solid #d7d7d7; /* 2px */
    background-color: rgb(253, 255, 253);
    color: #000000;
    border-color: rgb(80, 41, 89);
    transform: scale(1.3,1.3);
  }
  
  .nav{
    z-index: 0;
    max-width: 800px;
    transition: all 400ms;
  }